$myBlack: #04101A;
$myBlackSecondary: #363F47;

html {
  font-size: 100%;
}

.Container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Page {
  display: grid;
  height: 95vh;
  grid-template-rows: 7% 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "nav nav nav nav"
    "bigLukas bigLukas head head"
    "desc desc head head"
    "thanks bigVisk bigVisk bigVisk";
  padding: 0px;
  margin-bottom: 50px;
}

.NavBar {
  grid-area: nav;
  display: flex;
  height: 100%;

}

.Logo {
  width: 10vw;
  padding: 0%;
  margin: 20px;
  position: fixed;
  order: 1;
}

.Big-Lukas {
  grid-area: bigLukas;
  font-size: 15vw;
  font-family: futura-pt-bold, sans-serif;
  font-weight: 700;
  color: $myBlack;
  height: 30vh;
  width: 100%;
  text-align: left;
  padding: 0px;
  margin: 0px;
}
.Blurb {
  display: flex;
  flex-direction: column;
  grid-area: desc;
  margin: 0px;
  padding: 10px;
  place-self: center;
}

.Title {
  font-size: 4vw;
  color: $myBlack;
  font-family: futura-pt, sans-serif;
  font-weight: 800;
  padding: 0px;
  margin: 0px;
}

.Subtitle {
  padding: 0px;
  margin: 0px;
  font-size: 1.75vw;
  font-family: futura-pt, sans-serif;
  font-weight: 700;
  color: #D26463;
}

.Description {
  font-size: 1.25vw;
  font-family: futura-pt, sans-serif;
  font-weight: 500;
  color: $myBlackSecondary;
  padding: 0px;
  margin: 10px;
}

.Social-Icons {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

}

#spotify {
  margin: 5px;
  color: #3C8D8B;
}

#instagram {
  margin: 5px;
  color:  #D26463;
}

#linkedin {
  margin: 5px;
  color: #3F7EBF;
}

.Self {
  grid-area: head;
  width: 60%;
  padding: 0px;
  margin: 0px;
  place-self: center;
}

.Big-Viskanta {
  grid-area: bigVisk;
  font-size: 15vw;
  font-family: futura-pt-bold, sans-serif;
  font-weight: 700;
  color: white;
  height: 30vh;
  text-align: right;
  order: 2;
  padding: 0px;
  margin: 0px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

}

.Thanks-For {
  grid-area: thanks;
  animation: spin 8s infinite linear;
  align-self: start;
  justify-self: center;
  width: 15vw;
  object-fit: cover;
  padding: 0px;
  margin: 0px;
  order: 2;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.Second-Page {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    height: 100vh;
    width: 95vw;
    margin-top: 5vh;
    column-gap: 15px;
    padding: 15px;
  }


.Nike {
  grid-row: 1/2;
  grid-column: 1/2;
  place-self: center;
}

.Glue {
  grid-row: 1/2;
  grid-column: 2/3;
  place-self: center;
}

.Backup {
  grid-row: 1/2;
  grid-column: 3/4;
  place-self: center;
}

.Destroyer {
  grid-row: 1/2;
  grid-column: 4/5;
  place-self: center;
}

.Tiger {
  grid-row: 1/2;
  grid-column: 5/6;
  place-self: center;
}

.Chucky {
  grid-row: 1/2;
  grid-column: 6/7;
  place-self: center;
}

.Woman {
  grid-row: 2/3;
  grid-column: 1/2;
  place-self: center;
}

.Colosseum {
  grid-row: 2/3;
  grid-column: 2/3;
  place-self: center;
}

.DesignTitle {
  grid-row: 2/3;
  grid-column: 3/5;
  text-align: center;
  place-self: center;
  font-family: futura-pt-bold, sans-serif;
  font-size: 7vw;
}

.Statue {
  grid-row: 2/3;
  grid-column: 5/6;
  place-self: center;
}

.Crunch {
  grid-row: 2/3;
  grid-column: 6/7;
  place-self: center;
}

.Greece {
  grid-row: 3/4;
  grid-column: 1/2;
  place-self: center;
}

.David {
  grid-row: 3/4;
  grid-column: 2/3;
  place-self: center;
}

.Koolaid {
  grid-row: 3/4;
  grid-column: 3/4;
  place-self: center;
}

.Captcha {
  grid-row: 3/4;
  grid-column: 4/5;
  place-self: center;
}

.Film {
  grid-row: 3/4;
  grid-column: 5/6;
  place-self: center;
}

.Rex {
  grid-row: 3/4;
  grid-column: 6/7;
  place-self: center;
}

.Footer {
  width: 100vw;
  background-color: $myBlack;
  color: white;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  padding: 15px;
}

.SocialIcons{
  grid-row: 1/2;
  grid-column: 1/2;
  justify-self: end;
  align-self: center;
  width: 15vw;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  color: white;
}

.SocialIcons i {
  color: white;
}

.Transparentlogo {
  width: 10vw;
  grid-row: 1/2;
  grid-column: 2/3;
  justify-self: start;
  align-self: center;
}

.Copyright {
  grid-row: 2/3;
  grid-column: 1/3;
  justify-self: center;
  font-family: futura-pt, sans-serif;
  font-weight: 500;
}



@media only screen and (max-width: 830px) {

  .Page {
    display: grid;
    height: 100vh;
    grid-template-rows: 7% 1fr 1fr 15%;
    grid-template-columns: 0px 1fr 1fr 0px;
    grid-template-areas:
            "nav nav nav nav"
            ". head head ."
            ". desc desc ."
            ". thanks thanks .";
    padding: 0px;
    margin: 0px;
  }

  .Logo {
    width: 20vw;
    padding: 0%;
    margin: 20px;
    position: fixed;
  }

  .Self {
    grid-area: head;
    width: 50%;
    padding: 0px;
    margin: 0px;
    place-self: center;
  }

  .Title {
    font-size: 8vw;
    color: $myBlack;
    padding: 0px;
    margin: 0px;
  }

  .Subtitle {
    font-size: 4vw;
    color: #D26463;
    padding: 0px;
    margin: 0px;
  }

  .Description {
    font-size: 3vw;
    color: $myBlackSecondary;
    padding: 0px;
    margin: 10px;
  }

  .Thanks-For {
    grid-area: thanks;
    animation: spin 8s infinite linear;
    place-self: center;
    width: 40%;
    object-fit: cover;
    padding: 0px;
    margin: 0px;
  }

  .Big-Lukas {
    display: none;
  }

  .Big-Viskanta {
    display: none;
  }

  .Second-Page {
    display: grid;
    grid-template-rows: 5% 90% 5%;
    grid-template-columns: 1fr;
    height: 700vh;
    margin-top: 20vh;

  }

  .Designs-Title {
    color: white;
    font-size: 14vw;
    grid-row: 1/2;
    grid-column: 1/2;
    place-self: center;
    background-color: $myBlack;
    width: 100vw;
    text-align: center;

  }

  .Designs {
    display: flex;
    flex-wrap: wrap;
    margin-top: 6vh;
    padding: 0 25px;
    justify-self: center;
    align-self: flex-start;

  }

  .Col {
    flex: 100%;
    max-width: 100%;
  }

  .Rex {
    display: none;
  }

  .Chucky {
    display: none;
  }

  .Film {
    display: none;
  }
}





